@import url('https://fonts.googleapis.com/css2?family=Londrina+Solid:wght@300&family=Roboto&display=swap');
*{
  font-family: 'Roboto', sans-serif;
  margin: 0;
  padding: 0;
}
body{
  display: flex;
  place-items: center;
  place-content: center;
  height: 100vh;

  background: rgb(42,92,191);
background: linear-gradient(90deg, rgba(42,92,191,1) 0%, rgba(44,111,250,1) 46%, rgba(32,71,194,0.9836309523809523) 69%);
}
.App{
  display: flex;
  flex-direction: column;
  place-items: center;
  place-content: center;
  font-family: sans-serif;
  background-color: white;
  padding: 1.5rem;
  border-radius: 1rem;
  box-shadow: 5px transparent black;
  width: 65vw;
}
.row{
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}
.row p {
  font-size: .8rem;
}
.title{
  font-size: 2rem;
  color: black;
  margin: 1.2rem;
text-align: center;
}
.right {
  color: green;
}
.wrong {
  color: red;
}
ol {
  margin: 2rem;
  list-style-position: inside;
}
li{
  text-align: center;
   font-size: 1.3rem;
  margin: 1rem;
  color: black;
  cursor: pointer;
}
li:hover{
  background-color: lightyellow;
}
@media(max-width: 600px){
  .title{
    font-size: 1.6rem;
  }
  li{
    font-size: 1.3rem;
  }
}